import React, { Component } from 'react';
import { connect } from 'react-redux';
import { generateStatisticsReport, setListWhoseReportGenerated } from "../../../actions";

import './MeasurePerformanceModal.css';

class MeasurePerformanceModal extends Component {

    componentDidMount() {
        this.hideAllErrorMessages();
    }
    hideAllErrorMessages = () => {
        let errorMessages = document.getElementsByClassName("errorMessage");
        for (let i = 0; i < errorMessages.length; i++) {
            errorMessages[i].style.display = "none";
        }
    }
    checkifFieldsAreEmpty = () => {
        const hashtag = document.getElementById("hashtag").value;
        const listName = document.getElementById("performancelistName").value;
        let csvFile = document.getElementById("csvInfluencerFile").value;
        if (hashtag === "") {
            let errorMessage = document.getElementById("errorMessageHashtag");
            errorMessage.style.display = "block";
            return true;
        } else if ((listName === "") && (csvFile === "")) {
            let errorMessage = document.getElementById("errorMessageListFileEmpty");
            errorMessage.style.display = "block";
            return true;
        } else if (listName !== "") {
            if (this.checkIfListNameExists(listName)) {
                return false
            } else {
                let errorMessage = document.getElementById("errorMessageListNotExists");
                errorMessage.style.display = "block";
                return true;
            }
        } else {
            let errorMessageList = document.getElementById("errorMessageListFileEmpty");
            errorMessageList.style.display = "none";
            let errorMessageHashTag = document.getElementById("errorMessageHashtag");
            errorMessageHashTag.style.display = "none";
            return false;
        }
    }
    updateFollowers = followers => {
        this.setState(state => ({
            totalReach: state.totalReach + followers
        }));
    }
    calculateSummaryInformation(influencersJson) {
        influencersJson.map(influencer => {
            this.updateFollowers(influencer.followers);
        })
    }

    checkIfListNameExists(listName) {
        let alreadyNamedLists = this.props.listNamesArray;
        if (alreadyNamedLists) {
            if (alreadyNamedLists.includes(listName)) {
                return true
            } else {
                return false;
            }
        }

    }
    changehandler(e) {

        let files = e.target.files; // FileList object
        let csvFile = files[0];
        if (csvFile) {
            var reader = new FileReader();
            reader.onload = (function (theFile) {
                return function (e) {
                    document.querySelector('#csvcontent').value = e.target.result;
                };
            })(csvFile);
            reader.readAsText(csvFile);
        }
    }
    generateReport() {
        const result = this.checkifFieldsAreEmpty();
        if (!result) {
            let listName = document.getElementById("performancelistName").value;
            let csvfilecontent = document.querySelector("#csvcontent").value;
            const hashTagListInfo = {
                userCredentials: this.props.userCredentials,
                hashTag: "#" + document.getElementById("hashtag").value,
                listName: listName,
                csvfilecontent: csvfilecontent
            }
            this.props.setListWhoseReportGenerated(listName);
            this.props.generateStatisticsReport(hashTagListInfo);
            document.getElementById("performancelistName").value = "";
            document.getElementById("hashtag").value = "";
            document.getElementById("csvInfluencerFile").value = "";
            window.$("#measurePerformance").modal("toggle");
            let body = document.getElementsByTagName("body");
            body[0].classList.remove("modal-open");
            this.hideAllErrorMessages();
            window.$("#Loader").modal("show");
        }
    }
    render() {
        return (
            <div className="modal fade" id="measurePerformance" tabIndex="-1" role="dialog" data-backdrop="false" data-background="false">
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <p className="text-bold">Enter the Campaign's hashtag and the Campaign's influencers List Name</p>
                            <div className="CreationModal">
                                <input type="text" id="hashtag" placeholder="Enter Hashtag" />
                                <span id="errorMessageHashtag" className="errorMessage">Required field</span>
                            </div>
                            <br />
                            <div className="CreationModal">
                                <input type="text" id="performancelistName" placeholder="Enter List Name" />
                                <span id="errorMessageListFileEmpty" className="errorMessage">Enter List name or Upload File</span>
                                <span id="errorMessageListNotExists" className="errorMessage">List Name not Exists</span>
                            </div>
                            <br />
                            <div className="CreationModal">
                                <label> Upload Your Influencers List</label>
                                <input type="file" size="100" accept=".csv, text/csv" id="csvInfluencerFile" onChange={(e) => { this.changehandler(e) }} />
                            </div>
                        </div>
                        <div className="modal-footer">
                            <button
                                type="button"
                                className="btn btn-primary"
                                onClick={() => { this.generateReport() }}
                            >Submit</button>
                            <button
                                type="button"
                                className="btn btn-secondary"
                                data-dismiss="modal">Close</button>
                        </div>
                    </div>
                </div>
                <input type="hidden" id="csvcontent" />
            </div >
        );
    }
}
const mapStateToProps = state => ({
    userCredentials: state.userCredentials,
    listNamesArray: state.listNamesArray,
});
const mapDispatchToProps = dispatch => {
    return {
        generateStatisticsReport: hashTagListInfo => {
            dispatch(generateStatisticsReport(hashTagListInfo));
        },
        setListWhoseReportGenerated: listname => {
            dispatch(setListWhoseReportGenerated(listname));
        }
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(MeasurePerformanceModal);
